export default{
	name:'testype',
	data() {
		return {
			phoneType: '正在检测...',
			smodel:''
		}
	},
	created() {
		uni.getSystemInfo({
			success: res =>{
				let s = res.model
				let t = s.indexOf('<')
				if(t > 0 && t < 15){
					s = s.substring(0, t)
					// console.log('1' + s + '*')
					this.phoneType = s
					this.smodel = s
					this.setStorage()
				} else {
					let r = s.indexOf('(')
					if( r > 0 ){
						s = s.substring(0, r)
						// console.log(s)
						this.phoneType = s
						this.smodel = s
						this.setStorage()
					} else {
						// console.log('3' + s)
						this.phoneType = s
						this.smodel = s
						this.setStorage()
					}
				}
			}
		})
	},
	methods:{
		onNext() {
			uni.navigateTo({
				url: '../../pages/selectmodel/selectmodel'
			})
		},
		bindRepair(){
			uni.navigateTo({
				url:'../../pages/selectmodel/selectmodel?model=' + this.smodel
			})
		},
		setStorage(){
			uni.setStorage({
				key:'model',
				data: this.smodel
			})
			uni.setStorage({
				key:'phonetype',
				data: this.smodel
			})
		},
		hwTypeTest(e){
			let s = e.slice(0,3)
			switch (s){
				case 'SCL' : s = '荣耀4A'
					break;
				case 'KIW' : s = '荣耀5X'
					break;
				case 'TAG' : s = '畅玩5S'
					break;
				case 'TIT' : s = '畅玩5'
					break;
				case 'H60' : s = '荣耀6'
					break;
				case 'PE ' : s = '荣耀6 Plus'
					break;
				case 'PLK' : s = '荣耀7'
					break;
				case 'ATH' : s = '荣耀7i'
					break;
				case 'KIW' : s = '荣耀5X'
					break;
				case 'MT7' : s = 'Mate 7'
					break;
				case 'P7-' : s = '畅玩5'
					break;
				case 'KNT' : s = '荣耀V8'
					break;
				case 'FRD' : s = '荣耀8'
					break;
				case 'NXT' : s = 'Mate 8'
					break;
				case 'EDI' : s = 'Note 8'
					break;
				case 'MHA' : s = 'Mate 9'
					break;
				case 'ALE' : s = 'P8 青春版'
					break;
				case 'GRA' : s = 'P8'
					break;
				case 'NEM' : s = '荣耀5c'
					break;
			}
		}
	}
}
